body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.black {
    color: #333;
    text-shadow: 0 0 1px white;
}

.white {
    color: white;
    text-shadow: 0 0 1px black;
}

.tile {
    width: 1em;
    height: 1em;

    padding: 0.2em;
    line-height: 1em;
    text-align: center;

    background-color: #ddd;
}

.row:nth-of-type(even) .tile:nth-of-type(odd),
.row:nth-of-type(odd) .tile:nth-of-type(even) {
    background-color: #777;
}

.selected,
.row:nth-of-type(even) .tile:nth-of-type(odd).selected,
.row:nth-of-type(odd) .tile:nth-of-type(even).selected {
    background-color: #aa0;
}

.legal,
.row:nth-of-type(even) .tile:nth-of-type(odd).legal,
.row:nth-of-type(odd) .tile:nth-of-type(even).legal {
    background-color: #0aa;
}

.king:after {
    content: '\265A';
}

.queen:after {
    content: '\265B';
}

.bishop:after {
    content: '\265D'
}

.rook:after {
    content: '\265C';
}

.knight:after {
    content: '\265E';
}

.pawn:after {
    content: '\265F';
}

.board {
    -webkit-transition: -webkit-transform 2s ease;
    transition: -webkit-transform 2s ease;
    transition: transform 2s ease;
    transition: transform 2s ease, -webkit-transform 2s ease;
}

.board .row {
    display: flex;
    flex-direction: row;
    margin: 0 auto;

    font-size: 2em;
}

.board .tile {
    /*transition: transform 1s ease-out;*/
}

.board.rotate {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.board.rotate .tile {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
.App {
  text-align: center;

  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
